import PropTypes from "prop-types"
import React, {useState} from "react"
import {HeaderWrapper, MenuBar, MenuButton, Nav, NavItem} from "./header.style"
import {graphql, useStaticQuery} from "gatsby"
import Img from 'gatsby-image';

const HeaderComponent = () => {
    const [toggle, setToggle] = useState(false);

    const toggleMenu = () => {
        setToggle(!toggle);
    }

    const data = useStaticQuery(graphql`
  query Images {
  image: file(relativePath: {eq: "logo.png"}) {
    id
    childImageSharp {
      fixed(width: 50) {
        ...GatsbyImageSharpFixed
      }
    }
  }
}  
  `)


    return (
        <HeaderWrapper>
            <Img fixed={data.image.childImageSharp.fixed}  alt='logo'/>
            <Nav show={toggle}>
                <NavItem to='/'>Home</NavItem>
                <NavItem to='/services'>Services</NavItem>
                <NavItem to='/#clients'>Clients</NavItem>
                <NavItem to='/404'>About Us</NavItem>
                <NavItem to='/404'>Sports Management</NavItem>
            </Nav>
            <MenuButton onClick={toggleMenu}>
                <MenuBar/>
                <MenuBar style={{margin: '7px 0', width: '20px'}}/>
                <MenuBar/>
            </MenuButton>
        </HeaderWrapper>

    )
}


HeaderComponent.propTypes = {
    siteTitle: PropTypes.string,
}

HeaderComponent.defaultProps = {
    siteTitle: ``,
}

export default HeaderComponent
